.header {
    padding: 20px 0;
    &__wrapper {
        width: 100%;
        height: 100%;
    }
    &__nav {
        li {
            padding: 10px 20px;
            border-radius: 25px;
            transition: background-color .2s linear;
            @media (hover: hover) {
                &:hover {
                    background-color: var(--color-gray-100);
                }
                
            }
        }
    }
    &__actions {
        display: flex;
        gap: 15px;
    }
}

@media screen and (max-width: 1199.99px) {
    .header__actions {
        gap: 10px;
    }
    .header__nav li {
        padding: 10px;
    }
}